@import url(https://fonts.googleapis.com/css?family=Open+Sans);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

p {
    margin: 0 !important;
}

.anima {
    -webkit-animation: slideUp .3s linear;
            animation: slideUp .3s linear;
    right: 15px;
}

@-webkit-keyframes slideUp {
    from {
        right: -5%;
        visible: hidden;
    }
    to {
        visibility: visible;
        right: 15px;
    }
}

@keyframes slideUp {
    from {
        right: -5%;
        visible: hidden;
    }
    to {
        visibility: visible;
        right: 15px;
    }
}

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background-color: #000;
}

::-webkit-scrollbar-thumb {
    background: #ff5e14;
}

::selection {
    background: #ff5e14;
    color: #fff;
}

html,
body {
    scroll-behavior: smooth;
    overflow-x: hidden;
    width: 100vw;
}

.prodTitle {
    word-break: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}

.prodPara {
    word-break: break-word;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}

.webLogo {
    width: 80px;
    margin-left: 15px;
}

.products-container {
    width: 1250px;
    max-width: 1250px;
    padding: 50px 15px;
    margin: 15px auto;
    display: -webkit-flex;
    display: flex;
    background-color: rgb(223, 223, 223);
}

.pr-head {
    position: relative;
}

#userimg:hover {
    opacity: .7;
}

.products-card {
    width: 300px;
    margin: 5px;
    border: 1px solid #000;
    padding: 10px 0;
    border-radius: 5px;
    height: 400px;
    max-height: 400px;
    background-color: #fff;
    overflow: hidden;
}

.products-card img {
    width: 100%;
    height: 200px;
    object-fit: contain;
    background-color: #fff;
    border-bottom: 1px solid;
    padding-bottom: 5px;
}

.product-card-details {
    height: 200px;
    padding: 5px 10px;
}

.products-card button {
    width: 100%;
    padding: 8px;
    border: none;
    outline: none;
    font-size: 18px;
    font-weight: 700;
    background-color: cadetblue;
    cursor: pointer;
    overflow: hidden;
